<template>
    <div>
        <excel-qr v-if="currentStep === steps.findIndex(step=>step.key === 'excelQR')" :next-function="qrComplete"></excel-qr>
    </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex';
import excelQr from './../components/excelQr';

export default {
    name: 'exitEntryExcel',
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep'
        })
    },
    components: { excelQr },
    mounted () {
        this.setSteps([{ name: '扫码填表', isActive: true, visible: true, key: 'excelQR' }]);
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage', 'setDogHandlePic', 'changeVisible']),
        qrComplete () {
            this.changeTipPage({ message: '数据提交可能需要等待1-2分钟， 在这期间，您可返回首页办理其他业务', imgName: 'imgText' });
        }

    }
};

</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        justify-content: center;
        align-items: center;
    }
</style>
